body {
	margin: 0;
	color: #444;
	border-top: 2px solid #34AA33;
}

input {
	color: #444;
	border-style: none;
	outline: none;
	outline-style: none;
	-webkit-box-shadow: none;
	        box-shadow: none;
	line-height: 1rem;
}

button{
	color: #444;
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	line-height: 1rem;
}

a {
  text-decoration: none;
  color: white;
}



